<template>
	<li class="menu-list-item" @click="toggleChildList" :class="{'item-add-icon':isRoot,'item-plus-icon':open}">
		<span v-if="isRoot">{{item.msg}}</span>
		<router-link v-if="!isRoot" :key="item.id" :to="'/'+item.to">{{item.msg}}</router-link>
		<ul class="menu-list-item-child-container" v-show="open" v-if="isRoot">
			<menuListLeftItem v-for="child in item.children" :item="child" key="child.id"/>
		</ul>
	</li>
</template>

<script>
	export default{
		name:'menuListLeftItem',
		props:['item'],
		data(){
			return {
				open:false
			}
		},
		computed:{
			isRoot:function(){
				return this.item.children && this.item.children.length;
			}
		},
		methods:{
			toggleChildList:function(){
				if(this.isRoot){
					this.open = !this.open
				}				
			}
		}
	}
</script>

<style>
	.menu-list-item{
		border-bottom: 1px solid #e7e7e7;
		text-indent: 10px;
		position: relative;
		right: 0;
	}
	.menu-list-item:last-of-type{
		border-bottom: none;
	}
	.menu-list-item>a{
		text-decoration: none;
		color: #6C6C6C;
		display: block;
	}
	.item-add-icon:after{
		content: '+';
		position: absolute;
		top: 0;
		right: 10px;
		width:20px;
		text-align: center;
		font-weight: bold;
		font-size:18px;
	}
	.item-plus-icon:after{
		content: '-';
		position: absolute;
		top: 0;
		right: 10px;
		width:20px;
		text-align: center;
		font-weight: bold;
		font-size:18px;
	}
	.menu-list-item-child-container{
		margin:0;
		padding:0;
		list-style: none;
		font-size: 12px;
	}
	.menu-list-item-child-container .menu-list-item{
		padding:0 0 0 5px;
		background: #fff;
	}
	.menu-list-item-child-container .menu-list-item:first-child{
		border-top: 1px solid #e7e7e7;
	}
</style>


