<template>
<div>
	<h1>显示日期：</h1>
    <div>{{time}}</div>	
</div>
</template>

<script>
	export default{
		name:'DateClockOne',
		data(){
			return {
				time:''
			}
		},
		mounted:function(){
			this.showTime();
		},
		methods:{
			showTime(){
				var myDate = new Date(),
                    year = myDate.getFullYear(),
                    month = myDate.getMonth(),
                    date = myDate.getDate(),
                    weekday = new Array(7),
                    d = myDate.getDay(),
                    h = myDate.getHours(),
                    m = myDate.getMinutes(),
                    s = myDate.getSeconds();
	            m = this.checkTime(m);
	            s = this.checkTime(s);
	            weekday[0] = '星期日';
	            weekday[1] = '星期一';
	            weekday[2] = '星期二';
	            weekday[3] = '星期三';
	            weekday[4] = '星期四';
	            weekday[5] = '星期五';
	            weekday[6] = '星期六';

            	this.time = year+'年'+month+'月'+date+'日 '+weekday[d]+h+':'+m+':'+s;
            	setTimeout(this.showTime,500);
	           
	             /*console.log('年: '+myDate.getFullYear());
		        console.log('月: '+(myDate.getMonth()+1));
		        console.log('日: '+myDate.getDate());
		        console.log('星期: '+myDate.getDay());
		        console.log('小时: '+myDate.getHours());
		        console.log('分钟: '+myDate.getMinutes());
		        console.log('秒: '+myDate.getSeconds());
		        console.log('毫秒: '+myDate.getTime());*/

			},
			checkTime(i){
				 if(i<10){
	                i = '0'+i;
	            }
	            return i;
			}
		}
	}
</script>

<style>
</style>


