<template>
<div>
	 <h1>倒计时：</h1>
    <div>距离 {{distance}} 还有：{{txt}}</div>
</div>
</template>

<script>
	export default{
		name:'DateClockThree',
		data(){
			return {
				txt:'',
				distance:''
			}
		},
		mounted(){
			this.showTime();
		},
		methods:{
			showTime(){
				var curtime = new Date();
            	var endtime = new Date('2018/6/6,12:20:12');
	            var lefttime = parseInt((endtime.getTime() - curtime.getTime())/1000);
	            var d = parseInt(lefttime/(24*60*60));
	            var h = parseInt(lefttime/(60*60)%24);
	            var m = parseInt(lefttime/60%60);
	            var s = parseInt(lefttime%60);
	            this.distance = endtime;
	            if(lefttime <= 0){
	                this.txt = '结束';
	            }else{
	                this.txt = d+'天'+h+'小时'+m+'分'+s+'秒';
	                setTimeout(this.showTime,500);
	            }
			}
		}
	}
</script>

<style>
</style>


