<template>
	<div>
		<h1>倒计时：</h1>
    	<div>距离 {{now}} 还有：{{date}}天</div>
	</div> 	
</template>

<script>
	export default{
		name:'DateClockTwo',
		data(){
			return{
				date:'0',
				now:0
			}
		},
		mounted:function(){
			var curtime = new Date();
            var endtime = new Date('2018,6,6');
            var lefttime = Math.ceil((endtime.getTime() - curtime.getTime())/(24*60*60*1000));
            this.date = lefttime;
            this.now = endtime;
		}
	}
</script>

<style>
	
</style>


