import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import PopupMask from '@/components/PopupDemo'
import JqBar from '@/components/StaticBarOnRight/JqBar'
import JsBar from '@/components/StaticBarOnRight/JsBar'
import ChangePanelDemo from '@/components/ChangePanelDemo'
import Hello from '@/components/Hello'
import DateClockOne from '@/components/Date/DateClockOne'
import DateClockTwo from '@/components/Date/DateClockTwo'
import DateClockThree from '@/components/Date/DateClockThree'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children:[{
      	path: '',
      	component: Hello,
      },{
      	path: 'popupDemo',
	      component: PopupMask
      },{
      	path:'changePanelDemo',
      	component:ChangePanelDemo
      },{
      	path:'dateClockOne',
      	component:DateClockOne
      },{
      	path:'dateClockTwo',
      	component:DateClockTwo
      },{
      	path:'dateClockThree',
      	component:DateClockThree
      }]
    },{
    	path: '/StaticBarWithJquery',
      component: JqBar
    },{
    	path: '/StaticBarWithJs',
      component: JsBar
    }
  ]
})



