<template>
	<div id="app">
		<header class="header">
			<h1>demo示例</h1>
		</header>
	  	<section class="clearfix">
	  		<div id="leftContainer">
		  		<menulistleft :list ='dataList'/>
		  	</div>
		    <div id="rightContainer">
		    	<router-view></router-view>
		    </div>
	  	</section>
	</div>
  
</template>

<script>
	import {List} from '../../static/data.js';
	import menulistleft from './MenuListleft'
	export default {
	  name: 'main',
	  data(){
	  	return {
	  		dataList:List
	  	}
	  },
	  components:{menulistleft}
	}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
}
.header{
	line-height: 60px;
	font-size:25px;
	text-align: center;
}
#leftContainer{
	width:200px;
	float: left;
	height: 100%;
	padding: 10px;
}
#rightContainer{
	float:left;
	padding:15px 0;
}
</style>


